import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders, HttpErrorResponse } from '@angular/common/http';

const httpOptions = {
  headers: new HttpHeaders({
    'Content-Type': 'application/json'
  })
}


@Injectable()
export class WarehousesService {

  constructor(private http: HttpClient) { }

  getWhs(page) {
    return this.http.get<any>('http://157.230.14.37:8000/api/warehouses?page=' + page);
  }

  getWh(id){
    return this.http.get<any>('http://157.230.14.37:8000/api/warehouses/' + id);
  }

  createWh(wh) {
    return this.http.post<any>('http://157.230.14.37:8000/api/warehouses/', wh, httpOptions);
  }
}
