import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { WarehousesService } from '../../services/warehouses.service';
import { BoxesService } from '../../services/boxes.service';
@Component({
  selector: 'app-warehouse',
  templateUrl: './warehouse.component.html',
  styleUrls: ['./warehouse.component.css']
})


export class WarehouseComponent implements OnInit {
  private sub: any;
  private wh: any;
  id : number;
  correcto = false; 

  box_height: number;
  box_width: number;
  box_length: number;
  box_weight: number;
  box_description: string;

  cargado = false;

  

  constructor(private boxService: BoxesService, private whService: WarehousesService, private router: Router, private route: ActivatedRoute) {}

  ngOnInit() {
    this.sub = this.route.params.subscribe(params => {
      this.id = +params['id']; // (+) converts string 'id' to a number
      this.whService.getWh(this.id).subscribe(data => {
        this.wh = data;
        this.cargado = true;
      },
      err => {
      })
   });
  }

  crearCaja(){
    const newBox = {
      height: this.box_height,
      width: this.box_width,
      length: this.box_length,
      description: this.box_description,
      warehouse: this.id
    };

    this.boxService.createBox(newBox).subscribe(data => {
    }, 
    err => {
    })
  }
}
