import { Component, OnInit, HostListener, ElementRef } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { WarehousesService } from '../../services/warehouses.service';

@Component({
  selector: 'app-warehouses',
  templateUrl: './warehouses.component.html',
  styleUrls: ['./warehouses.component.css']
})
export class WarehousesComponent implements OnInit {
  private whs = [];
  correcto = false;
  options_opened = true;
  box_height: number;
  box_width: number;
  box_length: number;
  box_weight: number;
  box_description: string;
  calculatedheight: number;
  completadosSwitch = true;
  page: number;



  constructor(private whService: WarehousesService, private router: Router) { }

  ngOnInit() {
    this.page = 1;
    this.calculatedheight = window.innerHeight;
    this.whService.getWhs(this.page).subscribe(data => {
      if (data.count != 0) {
        this.whs = data.results;
      }
    },
      err => {
        console.log(err);
      });
  }


  @HostListener('window:resize', ['$event'])
  onResize(event) {
    this.calculatedheight = window.innerHeight;
  }

  next_page = function () {
    this.page++;
    this.whService.getWhs(this.page).subscribe(data => {
      if (data.count != 0) {
        this.whs = data.results;
      }
    },
      err => {
        console.log(err);
      });
  }

  prev_page = function () {
    this.page--;
    this.whService.getWhs(this.page).subscribe(data => {
      if (data.count != 0) {
        this.whs = data.results;
      }
    },
      err => {
        console.log(err);
      });

  }

  open_options = function () {
    this.options_opened = !this.options_opened;
  }
}

